body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  
  
}

body, #root, .App {  
  width: 100%;
  height: 100%;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;  
  background: #cb2d3e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ef473a, #cb2d3e);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ef473a, #cb2d3e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.box {
  background: #fff;
  border-radius: 5px;
  display: flex;
  max-width: 800px;
  justify-content: center;
  flex-wrap: wrap;
  box-shadow: 1px 1px 25px -10px #000000cf;
}

.md {
  width: 45%;
  padding: 10px;
}

.full {
  width: 100%;
  padding: 10px;
}

.flex-center {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.map {
  border: 1px solid #cecece;
  border-radius: 2px;
  padding: 10px;
  margin: 5px;
  min-width: 30%;
  font-weight: 700;
}

.map span {
  display: block;
  font-size: .7em;
  font-weight: 500;
}

.ant-modal-content {
  border-radius: 5px;
}